import { loadFont } from '@doonce/utils'

import {
  Base,
  HorizontalAlignEnum,
  IContent,
  IHorizontalAlign,
  IPos,
  ISize,
  LayoutItemTypeEnum
} from '../base'

export class GraphTitle extends Base implements IContent, IHorizontalAlign {
  layoutItemType = LayoutItemTypeEnum.GRAPH_TITLE
  canLineBreak = false

  rawContent: string = ''
  content: string = ''

  horizontalAlign: HorizontalAlignEnum = HorizontalAlignEnum.MIDDLE

  getPos(): IPos {
    return {
      x: this.x,
      y: this.y
    }
  }

  async getSize(): Promise<ISize> {
    throw new Error('Method not implemented.')
  }
}
